import Vue from 'vue'
import './plugins/vuetify'
import App from './App.vue'
import VueRouter from 'vue-router'

import Story from './components/Story'
import Photography from './Photography'
import Index from './Index'
import Team from './components/Team'
import Partner from './Partner'
import Motion from './Motion'
import Client from './Client'
import Article from './components/Article'

import VueImageSwipe from 'vue-image-swipe'
import 'vue-image-swipe/dist/vue-image-swipe.css'

import AsyncComputed from 'vue-async-computed'

import config from '../config.js'

Vue.use(VueImageSwipe)
Vue.config.productionTip = false
Vue.use(VueRouter)
Vue.use(AsyncComputed)

const routes = [
  { path: config.baseurl + '/', component: Index },
  {
    path: config.baseurl + '/photography', component: Photography,
    children: [
      {
        path: 'detail',
        component: undefined
      }
    ]
  },
  {
    path: config.baseurl + '/story', component: Story,
    children: [
      {
        path: 'article',
        component: Article
      }
    ]
  },
  { path: config.baseurl + '/team', component: Team },
  { path: config.baseurl + '/partner', component: Partner },
  { path: config.baseurl + '/detail', component: Photography },
  { path: config.baseurl + '/motion', component: Motion },
  { path: config.baseurl + '/client', component: Client }
]

const router = new VueRouter({
  mode: 'history',
  routes
})

new Vue({
  render: h => h(App),
  router
}).$mount('#app')
